<template>
  <div class="ao-asset-transfer">
    <ao-asset-return></ao-asset-return>
    <!-- 内容 -->
    <div class="content">
      <div class="title">{{ transfer_title }}</div>
      <div class="currency">
        <div
          class="currency-item"
          :class="[index === currencyIndex && item.count == 2 ? 'active' : '']"
          v-for="(item, index) in currencyList"
          :key="index"
          @click="setMoneys(index)"
        >{{ item.name }}</div>
      </div>
      <div class="info">
        <div class="left">
          <div class="left-item">
            <span>{{ transfer_text[0] }}</span>
            <span>{{ values[0] }}</span>
          </div>
          <div class="left-item">
            <span>{{ transfer_text[1] }}</span>
            <span>{{ values[1] }}</span>
          </div>
        </div>
        <img :src="iconchange" @click="change" />
      </div>
      <div class="input-box">
        <input :placeholder="transfer_text[2]" type="number" v-model="number" />
        <div class="right">
          <span
            class="unit"
          >{{ currencyList[currencyIndex] ? currencyList[currencyIndex].name : '' }}</span>
          <span class="all" @click="all">{{ transfer_btn[0] }}</span>
        </div>
      </div>
      <div class="balance">
        <span>{{ getValue(0) }}{{ transfer_text[3] }}</span>
        <span>{{ moneys[0] }}</span>
      </div>
      <div class="balance">
        <span>{{ getValue(1) }}{{ transfer_text[3] }}</span>
        <span>{{ moneys[1] }}</span>
      </div>
      <el-button type="primary" @click="transfer">{{ transfer_btn[1] }}</el-button>
    </div>
  </div>
</template>

<script>
import AoAssetReturn from "./components/AoAssetReturn";
import iconchange from "@/assets/icon-change.png";
import { geTransfer, assetTransfer } from "@/api/asset";

export default {
  components: {
    AoAssetReturn
  },
  data() {
    return {
      transfer_title: this.$t('asset.transfer_title'),
      transfer_btn: this.$t('asset.transfer_btn'),
      transfer_text: this.$t('asset.transfer_text'),
      iconchange: iconchange,
      currencyIndex: 0,
      currencyList: [],
      list: [],
      typesId: 1,
      tid: 0,
      types: [],
      moneys: [0, 0],
      number: ""
    };
  },
  computed: {
    values() {
      var vvvv = ["", ""];
      if (this.types.length > 0) {
        for (var i = 0; i < this.types.length; i++) {
          if (this.types[i].id == this.typesId) {
            vvvv[0] = this.types[i].value;
          } else {
            vvvv[1] = this.types[i].value;
            this.tid = this.types[i].id;
          }
        }
      }
      return vvvv;
    }
  },
  mounted() {
    var __data = JSON.parse(localStorage.getItem("jyxt_asset_table_path")); //this.$store.state.__data;
    if (!__data) {
      this.$router.back(-1);
    }
    this.typesId = __data.type;
    this.type = __data.type;
    geTransfer({ type: __data.type }).then(res => {
      this.currencyList = res.currency;
      this.list = res.list;
      this.types = res.types;
      this.setMoneys(0);
    });
  },
  methods: {
    transfer() {
      var data = {
        number: this.number,
        currency: this.currencyList[this.currencyIndex].name,
        fid: this.typesId,
        tid: this.tid,
        type: this.type
      };
      assetTransfer(data).then(res => {
        this.$router.back(-1);
      });
    },
    all() {
      this.number = parseFloat(this.moneys[this.typesId - 1]);
    },
    ffff(v, l = 8) {
      return parseFloat(v).toFixed(l);
    },
    setMoneys(index) {
      if (this.currencyList[index].count == 1) return;

      this.currencyIndex = index;

      var m = [0, 0];
      var arr = this.list[this.currencyList[this.currencyIndex].name];
      console.log(arr);
      this.types.forEach((o, i) => {
        var obj = arr.find(n => n.type == o.id);
        // console.log(obj)
        m[i] = obj ? obj.balance : 0;
      });
      this.moneys = [this.ffff(m[0]), this.ffff(m[1])];
    },
    getValue(index) {
      return this.types.length > 0 ? this.types[index].value : "";
    },
    change() {
      for (var index in this.types) {
        var o = this.types[index];
        if (o.id != this.typesId) {
          this.typesId = o.id;
          break;
        }
      }
    }
  }
};
</script>

<style scoped lang="scss">
.ao-asset-transfer {
  .content {
    width: 100%;
    padding: 0 30px;
    color: #c7cce6;
  }
  .title {
    padding: 20px 0;
    font-size: 24px;
    color: #61688a;
  }
  .currency {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }
  .currency-item {
    margin-right: 20px;
    padding: 5px 20px;
    color: #fff;
    background: #234165;
    margin-top: 8px;
    cursor: pointer;
    &.active {
      background: #7a98f7;
    }
  }
  .info {
    display: flex;
    align-items: center;
    .left {
      flex: 1;
      .left-item {
        line-height: 40px;
        display: flex;
        justify-content: space-between;
      }
    }
    img {
      margin: 25px;
      width: 30px;
      height: 30px;
      cursor: pointer;
    }
  }
  .input-box {
    display: flex;
    align-items: center;
    margin-top: 32px;
    input {
      flex: 1;
      border: none;
      color: #f2f5ff;
      background: none;
      line-height: 40px;
      width: 755px;
      border-bottom: 2px solid #3a4975;
      font-size: 100%;
    }
    .right {
      flex-grow: 0;
      .unit {
        color: rgb(97, 104, 138);
      }
      .all {
        margin-left: 10px;
        padding-left: 10px;
        border-left: 1px solid #ccc;
        cursor: pointer;
      }
    }
  }
  .balance {
    line-height: 60px;
    margin: 20px 0px 0px;
  }
  .el-button {
    flex: 1;
    width: 200px;
    line-height: 46px;
    background-color: #7a98f7;
    border-radius: 4px;
    color: #fff;
    border: none;
    padding: 0;
    margin: auto;
    display: block;
  }
}
</style>



